import { isJson } from '../utils';
import { fetch } from 'whatwg-fetch';

export const DEFAULT_JSON_METHOD = 'GET';
export const DEFAULT_JSON_HEADERS = { 'Accept': 'application/json', 'Content-Type': 'application/json', };
export const DEFAULT_JSON_BODY = null;

export default function fetchJson(url, options) {
  return fetch(url, options)
    .then((response) => {
      const contentType = response.headers.get('Content-Type');

      if (contentType && contentType.indexOf('application/json') < 0) {
        throw new TypeError('Content-Type of response is not application/json');
      }

      if (response.ok) {
        return response.text().then((text) => {
          response.json = isJson(text) ? JSON.parse(text) : null;

          return Promise.resolve(response);
        });
      }

      return response.text().then((text) => {
        response.json = isJson(text) ? JSON.parse(text) : null;

        return Promise.reject(response);
      });
    })
    .catch((error) => {
      if (typeof error !== 'object') {
        console.error(error);
      }

      return Promise.reject(error);
    });
}



