import {
  fetchJson,
  JSON_DEFAULT_METHOD,
  JSON_DEFAULT_HEADERS,
  JSON_DEFAULT_BODY
} from './json';

export default class Fetch {
  static json(url, options = { method: JSON_DEFAULT_METHOD, headers: JSON_DEFAULT_HEADERS, body: JSON_DEFAULT_BODY }) {
    return fetchJson(url, options);
  }
}



